import { useEffect, useState, useRef, useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Actions } from "../../redux/sagas/saga.actions";
import { Ifeeds, Istore } from "../../types";
import { useTheme } from "@mui/material";

const useHomeController = () => {
  const [displayFeeds, setDisplayFeeds] = useState<Ifeeds[]>([]);
  const [searchValue, setSearchValue] = useState<string>("");
  const dispatch = useDispatch();
  const theme = useTheme();
  const timerRef = useRef(null);

  const { feeds, isLoading, errorFetchingFeeds, metaData } = useSelector(
    (store: Istore) => ({
      feeds: store.feeds.feeds,
      isLoading: store.feeds.feedsLoading,
      errorFetchingFeeds: store.feeds.feedsErrorMessage,
      metaData: store.feeds.metaData,
    })
  );

  const fetchFeeds = (page: number) => {
    dispatch({ type: Actions.UPDATE_FEED, page });
  };

  const search = () => {
    if (!searchValue.trim()) {
      setDisplayFeeds(feeds);
      return;
    }
    const filteredData = feeds.filter((value) => {
      let searchTerm = searchValue.toLowerCase();
      let wordToSearch = value.name.toLowerCase();
      return wordToSearch.includes(searchTerm);
    });
    setDisplayFeeds(filteredData);
  };

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting) {
          fetchFeeds(
            parseInt(metaData["page-num-requested"] as any as string) + 1
          );
        }
      },
      { threshold: 1 }
    );

    if (timerRef.current) {
      clearTimeout(timerRef.current);
    }

    if (feeds.length > 0) {
      timerRef.current = setTimeout(() => {
        const elements = document.querySelectorAll(".cardListItem");
        if (elements.length > 0) {
          observer.observe(elements[elements.length - 7]);
        }
      }, 1000) as any;
    }

    return () => {
      const elements = document.querySelectorAll(".cardListItem");
      if (elements.length > 0) {
        observer.unobserve(elements[elements.length - 7]);
      }
      clearTimeout(timerRef.current as any);
    };
  }, [feeds]);

  // enables us to continue getting more pages and filter if there is a search value
  useEffect(() => {
    search();
  }, [feeds]);

  useEffect(() => {
    search();
  }, [searchValue]);

  const goBack = useCallback(() => {}, []);

  const setStaticHtmlBg = () => {
    const htmlBody = document.querySelector("body");
    htmlBody!.style.background = theme.palette.primary.main;
  };

  // calls Api on init
  useEffect(() => {
    fetchFeeds(1);
    setStaticHtmlBg();
  }, []);

  return {
    displayFeeds,
    isLoading,
    errorFetchingFeeds,
    metaData,
    setSearchValue,
    search,
    goBack,
  };
};

export { useHomeController };
