import { EnVars } from "./validateEnv";

export const imageUrl = (image: string) => {
  return `${EnVars.REACT_APP_API_URL}/images/${image}`;
};

export const imageDimention = () => {
  const { innerWidth } = window;
  // width for an image
  const width = (innerWidth - 60) / 3; // deduct 60 for space that will be evenly distributed
  // height for an image with repect to ratio 2:3
  const height = (width * 3) / 2;
  return { width, height };
};

type imageItem = {
  key: number;
  name: string;
  "poster-image": string;
};

export const getMissingItems = (feedsLen: number) => {
  const lastRowCount = feedsLen % 3; // gives how many items are at last row;
  const items: imageItem[] = [];
  if (lastRowCount) {
    const itemToFill = 3 - lastRowCount;
    for (let i = 0; i < itemToFill; i++) {
      items.push({
        key: feedsLen + i,
        name: "",
        "poster-image": "",
      });
    }
  }
  return items;
};
