import { NavBar, Image, ListEmpty } from "../../components";
import { MainContainer, ImageListContainer } from "./styled-components";
import { useHomeController } from "./home.controller";
import { getMissingItems, imageDimention } from "../../utils/helpers";
import { CircularProgress } from "@mui/material";

const HomePage = () => {
  const { width, height } = imageDimention();

  const {
    metaData,
    setSearchValue,
    goBack,
    displayFeeds: feeds,
    isLoading,
  } = useHomeController();

  if (isLoading && feeds.length === 0) {
    return <CircularProgress />;
  }
  return (
    <MainContainer>
      <NavBar
        goBack={goBack}
        onSearch={setSearchValue}
        title={metaData.title}
      />
      <ImageListContainer>
        {feeds.map((feed, index) => (
          <Image key={index} item={feed} />
        ))}
        {/* Completes incompletes row */}
        {getMissingItems(feeds.length).map(({ key }) => (
          <div key={key} style={{ width, height }} />
        ))}
        <ListEmpty isEmpty={feeds.length === 0 && !isLoading} />
      </ImageListContainer>
    </MainContainer>
  );
};
export default HomePage;
