import { useTheme } from "@mui/material";
import { Typography } from "@mui/material";
import { ListEmptyContainer } from "./styled-components";

const ListEmptyComponent = ({ isEmpty }: { isEmpty: boolean }) => {
  const theme = useTheme();
  return isEmpty ? (
    <ListEmptyContainer>
      <Typography color={theme.palette.text.primary}>
        No Result Found!
      </Typography>
    </ListEmptyContainer>
  ) : null;
};
export default ListEmptyComponent;
