import { imageUrl } from "../../utils/helpers";
import { AppBarCompoent, AvatarComponent, TextField } from "./styled-component";
import { IconButton, Box, Toolbar } from "@mui/material";

type Props = {
  title: string;
  onSearch: (text: string) => void;
  goBack: () => void;
};
const NavBar = ({ title, onSearch, goBack }: Props) => {
  return (
    <Box flexGrow={1}>
      <AppBarCompoent position="fixed">
        <Toolbar>
          <IconButton aria-label="back-button" onClick={goBack}>
            <AvatarComponent src={imageUrl("Back.png")} />
          </IconButton>
          <TextField
            onChange={(e) => onSearch(e.target.value)}
            placeholder={title}
          />
          <IconButton>
            <AvatarComponent src={imageUrl("search.png")} />
          </IconButton>
        </Toolbar>
      </AppBarCompoent>
    </Box>
  );
};

export default NavBar;
