import { ImageListItem } from "@mui/material";
import { Ifeeds } from "../../types";
import { imageDimention, imageUrl } from "../../utils/helpers";
import { ImageCaption } from "./styled-components";
import { forwardRef } from "react";
type Props = {
  item: Ifeeds;
};

const Image = forwardRef(({ item }: Props, ref) => {
  const { width, height } = imageDimention();
  const handleErorEvent = (event: any) => {
    event.target.src = imageUrl("placeholder_for_missing_posters.png");
  };
  return (
    <ImageListItem
      className="cardListItem"
      ref={ref as any}
      style={{ marginBottom: 20, maxWidth: width }}
    >
      <img
        src={imageUrl(item["poster-image"])}
        alt={item.name}
        loading="lazy"
        onError={handleErorEvent}
        style={{
          width: `${width}px`,
          height: `${height}px`,
        }}
      />
      <ImageCaption>{item.name}</ImageCaption>
    </ImageListItem>
  );
});

export default Image;
