import { createSlice } from "@reduxjs/toolkit";
import { Ifeeds, IfeedsMetaData, ReducerAction } from "../../types";

const intialState = {
  feedsLoading: false,
  feedsErrorMessage: "",
  feeds: [] as Ifeeds[],
  metaData: {} as IfeedsMetaData,
};

type feedsPayload = {
  feeds: Ifeeds[];
  metaData: IfeedsMetaData;
};

const feedsSlice = createSlice({
  name: "feeds",
  initialState: intialState,
  reducers: {
    updateFeeds(state, action: ReducerAction<feedsPayload>) {
      if (action.payload.metaData["page-num-requested"] == 1) {
        state.feeds = [...action.payload.feeds];
      } else {
        state.feeds = [...state.feeds, ...action.payload.feeds];
      }
      state.metaData = action.payload.metaData;
      state.feedsLoading = false;
      state.feedsErrorMessage = "";
    },
    fetchFeeds(state) {
      state.feedsLoading = true;
      state.feedsErrorMessage = "";
    },
    errorLoadingFeeds(state, action: ReducerAction<string>) {
      state.feedsErrorMessage = action.payload;
      state.feedsLoading = false;
    },
    clearFeeds(state) {
      state.feeds = [];
    },
  },
});

export const { updateFeeds, fetchFeeds, errorLoadingFeeds, clearFeeds } =
  feedsSlice.actions;
export const feedsReducer = feedsSlice.reducer;
