import { useState } from "react";
import {
  createTheme,
  ThemeProvider as ThemeComponentProvider,
} from "@mui/material";
import { ThemeType, themeDefination, type Modes } from "./theme-config";

const ThemeProvider = ({
  children,
}: {
  children: React.ReactElement;
}): JSX.Element => {
  const [mode, setMode] = useState<Modes>(ThemeType.dark);

  const toggle = (): void => {
    const nextMode = mode === ThemeType.dark ? ThemeType.light : ThemeType.dark;
    setMode(nextMode);
  };
  const theme = createTheme({
    ...themeDefination(mode),
    toggle,
  });
  return (
    <ThemeComponentProvider theme={theme}>{children}</ThemeComponentProvider>
  );
};

export default ThemeProvider;
