/* eslint-disable @typescript-eslint/explicit-function-return-type */
import { put, takeLeading, call } from "redux-saga/effects";
import { type AxiosResponse } from "axios";
import { Actions } from "./saga.actions";
import { errorLoadingFeeds, fetchFeeds, updateFeeds } from "../reducers";
import { IfeedsApiResponse } from "../../types";
import Api from "../../apiSdk";

type updateFeedsPayload = {
  page: number;
};

export function* watchUpdateFeeds() {
  yield takeLeading(
    Actions.UPDATE_FEED,
    function* (action: any): Generator<any> {
      yield put(fetchFeeds());
      try {
        const feedsResponse = yield call(
          Api.getFeeds.bind(null, (action as updateFeedsPayload).page)
        );
        const response = feedsResponse as AxiosResponse<IfeedsApiResponse>;
        const { "content-items": contentItems, ...metaData } =
          response.data.page;
        yield put(updateFeeds({ metaData, feeds: contentItems.content }));
      } catch (e: any) {
        yield put(errorLoadingFeeds(e.message));
      }
    }
  );
}
